@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;1,100;1,200;1,300;1,400;1,500;1,600&display=swap);
body{
  font-family : Raleway,sans-serif;
  margin:0;
  font-size:1.5em;
  text-align:center;
  background:#1b1523;
  color:#fff;
}
.App{
  max-width:860px;
  margin:40px auto;
}
button{
  background:none;
  border:2px solid #fff;
  padding:6px 12px;
  border-radius:4px;
  color:#fff;
  font-weight:bold;
  cursor:pointer;
  font-size:1em;
}
button:hovre{
  background:#c23866;
  color:#fff;
}

.card-grid{
  margin-top:40px;
  display:grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap:20px;
}
.card{
  position:relative;
}
.card img{
  width:100%;
  display:block;
  border:2px solid #fff;
  border-radius:6px;
}

.card .front{
  transform:rotateY(90deg);
  transition:all ease-in-out 0.2s;
  position:absolute;
}
.flipped .front {
  transform:rotateY(0deg);
  transition-delay:0.2s;
}

.card .back{
  transition:all ease-in-out 0.2s;
  transition-delay:0.2s;
}
.flipped .back{
  transform:rotateY(90deg);
  transition-delay:0s;
}
